import './App.css';
import Quiz from './Quiz';
import Image from './Images/icon.png';
import Pdf from './docs/ ,    .pdf';
import Pdf1 from './docs/ ,  .pdf';
import Pdf2 from './docs/      .pdf';
import Pdf3 from './docs/        .pdf';
import Pdf4 from './docs/     .pdf';
import Pdf5 from './docs/       .pdf';
import Pdf6 from './docs/,       .pdf';
import Pdf7 from './docs/     .pdf';
import Pdf8 from './docs/     .pdf';
import Pdf9 from './docs/    .pdf';
import Pdf10 from './docs/     .pdf';
import {useState} from "react";
import {
  Button, Checkbox, FormGroup, FormLabel, FormControl, Radio, FormControlLabel, RadioGroup, Stack,
  Divider, Container, Typography, Box, Paper, Alert, Breadcrumbs, Link, Menu, MenuItem
} from '@mui/material'
import { styled } from '@mui/material/styles';

import map from 'lodash/map'
import size from 'lodash/size'
import forEach from 'lodash/forEach'
import filter from 'lodash/filter'

const MENU = [{
    title: ' ,    ', link: Pdf},
    {title: ' ,  ', link: Pdf1},
    {title: '      ', link: Pdf2},
    {title: '        ', link: Pdf3},
    {title: '     ', link: Pdf4},
    {title: '       ', link: Pdf5},
    {title: ',       ', link: Pdf6},
    {title: '     ', link: Pdf7},
    {title: '     ', link: Pdf8},
    {title: '    ', link: Pdf9},
    {title: '     ', link: Pdf10}
]



const QUESTION_TYPE = {
    single: 1,
    multiple: 2
}

const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    maxWidth: '80%'
}))

const plural = (forms, n) => {
    let idx;
    if (n % 10 === 1 && n % 100 !== 11) {
        idx = 0; // one
    } else if (n % 10 >= 2 && n % 10 <= 4 && (n % 100 < 10 || n % 100 >= 20)) {
        idx = 1; // few
    } else {
        idx = 2; // many
    }
    return forms[idx] || '';
}

const TextView = ({message}) => <Box sx={{ flexGrow: 1, overflow: 'hidden', px: 3 }}>
            <Item sx={{my: 1, mx: 'auto', p: 2}}>
                <Stack spacing={2} direction="row" alignItems="center">
                    <Typography className={'Box'} >{message}</Typography>
                </Stack>
            </Item>
        </Box>

function App() {
  const [userAnswers, setUserAnswers] = useState({})
  const [answerStatistic, setAnswerStatistic] = useState([])
  const handleChange = (question) => (event) => setUserAnswers({...userAnswers, [question.id]: Number(event.target.value)})

  const handleChangeCheckbox = (question, option) => (event) => {
    const _userAnswers = {...userAnswers}
    if (_userAnswers[question.id] && event.target.checked) {
      _userAnswers[question.id] = {..._userAnswers[question.id], [option.id]: event.target.checked}
    } else if (event.target.checked) {
      _userAnswers[question.id] = {[option.id]: event.target.checked}
    } else {
        delete _userAnswers[question.id][option.id]
    }
      setUserAnswers(_userAnswers)
  }

  const handleValidate = () => {
      let rightAnswers = 0
      let failedAnswers = 0
      let correctQuest = []
      forEach(Quiz.questions, (question) => {
          const correct = filter(question.answers, ['isCorrect', true])
          if (question.type === QUESTION_TYPE.single) {
              if (userAnswers[question.id] === correct[0].id) {
                  rightAnswers++
                  correctQuest.push(question.id)
              } else {
                  failedAnswers++
              }
          } else {
              const correctSize = size(correct)
              if (size(userAnswers[question.id]) === correctSize) {
                  let isRight = 0
                  forEach(correct, (i) => {
                      if (userAnswers[question.id][i.id]) {
                          isRight++
                      } else {
                          isRight--
                      }
                  })
                  if (isRight === correctSize) {
                      correctQuest.push(question.id)
                      rightAnswers++
                  } else {
                      failedAnswers++
                  }
              } else {
                  failedAnswers++
              }
          }
      })
      setAnswerStatistic([rightAnswers, failedAnswers])
  }

const renderRadio = (question) => <RadioGroup
      aria-labelledby="demo-radio-buttons-group-label"
      className="Radio"
      name="radio-buttons-group"
      onChange={handleChange(question)}
  >
      {map(question.answers, (option, index) =>
          <FormControlLabel key={index} value={option.id} className="FormControlLabel" control={<Radio />} label={option.name} />
          )}
  </RadioGroup>

    const renderCheckbox = (question) => <FormGroup>
        {map(question.answers, (option, index) =>
            <FormControlLabel key={index} control={<Checkbox onChange={handleChangeCheckbox(question, option)} />} label={option.name} />
        )}
    </FormGroup>
    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = (link) => () => {
        window.open(link, '_blank')
        setAnchorEl(null);
    };
  return (
    <div className="App">
      <header className="App-header">
          <img src={Image} className="App-logo" alt="logo" />
          <div className="Header">
          <h4></h4>
        <h5> ,   </h5>
          </div>
      </header>
        <Box sx={{ flexGrow: 1, overflow: 'hidden', px: 3 }}>
        <Item sx={{my: 1, mx: 'auto', p: 2}}>
        <Stack spacing={2} direction="row" alignItems="center">
            <Button
                id="basic-button"
                aria-controls={open ? 'basic-menu' : undefined}
                aria-haspopup="true"
                aria-expanded={open ? 'true' : undefined}
                onClick={handleClick}
            >
                ,    
            </Button>
            <Menu
                id="basic-menu"
                anchorEl={anchorEl}
                open={open}
                onClose={() => true}
                MenuListProps={{'aria-labelledby': 'basic-button'}}
            >
                {map(MENU, (item, ind)=> <MenuItem key={ind} onClick={handleClose(item.link)}>{item.title}</MenuItem>)}
            </Menu>
        </Stack>
        </Item>
        </Box>
      <Container maxWidth="md" className="Container">
      {Quiz.questions.map((question, k) => <Stack key={k} spacing={{ xs: 1, sm: 2, md: 4 }}>
           <FormControl key={k}>
              <FormLabel id="demo-radio-buttons-group-label" className="Label">{question.label}</FormLabel>
              {question.type === QUESTION_TYPE.single ? renderRadio(question) : renderCheckbox(question)}
           </FormControl>
          <Divider />
      </Stack>
      )}
      <Stack className="Button">
          {answerStatistic[0] === size(Quiz.questions)
              ? <Alert severity="success"> !</Alert>
              : answerStatistic[1] > 0
              ? <Alert severity="error">{plural([" ", " ", " "], answerStatistic[1]) + answerStatistic[1] + plural([" ", " ", " "], answerStatistic[1])}</Alert>
              : null
          }
      <Button disabled={size(userAnswers) < size(Quiz.questions)} onClick={handleValidate} variant="contained"></Button>
      </Stack>
          <TextView message={Quiz.text3} />
      </Container>
    </div>
  );
}

export default App;